    p.mt-5 La funcin que desempea el componente dentro de un circuito elctrico depende de las propiedades elctricas dotadas durante el proceso de fabricacin y de la finalidad prevista del circuito diseado.

    .titulo-segundo.mt-5
      #t_1_1.h2 1.1  Clasificacin
    .row.mt-5
      .col-12.col-lg-7
        p Los componentes electrnicos se pueden clasificar de diferentes maneras, pueden clasificarse por la funcin que desempean, es decir si son componentes activos, pasivos o de proteccin, pueden clasificarse por la lgica que manejen anlogos o digitales, pueden clasificarse por las magnitudes de la energa elctrica que manejan en componentes de alta potencia y baja potencia e incluso se pueden clasificar por la forma fsica de su cuerpo o encapsulado en componentes de insercin (#[strong THT por sus siglas en ingls]) y componentes de superficie (#[strong SMD por sus siglas en ingls]).
        p.mt-3 Esta ltima es una de las ms usadas en procesos de fabricacin y ensamble electrnico ya que abarca en su totalidad a todos los componentes electrnicos y en la mayora de los casos no obedecen a particularidades del componente (#[strong funcin que desempea, naturaleza de la energa, parmetros elctricos, entre otros]). 
        p.mt-3 A continuacin, se presenta una breve descripcin de los componentes electrnicos de acuerdo a su forma fsica o encapsulado. 
