    .titulo-segundo.mt-5
      #t_1_3.h2 1.3  Encapsulados
    .row.mt-4
      .col-12.col-lg-7
        p Representan el cuerpo fsico de los componentes electrnicos, son responsables de otorgar la forma, el tamao, la disposicin y cantidad de los pines conductores al componente. En la fase de diseo, el tamao de la placa de circuito impreso depende en gran medida de la eleccin de los componentes a partir de sus encapsulados. La variedad de encapsulados est dada principalmente por las magnitudes de energa elctrica que vayan a emplearse en la operacin del componente dentro del circuito elctrico. Es decir, entre ms alta sea la energa ms grande ser el encapsulado del componente.
        p.mt-3 A continuacin, se observan los encapsulados ms utilizados en circuitos integrados. 
      .col-6.col-lg-5
        figure
          img(src="@/assets/template/tema-1-7.png", alt="Texto que describa la imagen")
