    .titulo-segundo.mt-5
      #t_1_4.h2 1.4  Materiales
    figure.mt-3
      img(src="@/assets/template/tema-1-10.png", alt="Texto que describa la imagen")
    p.mt-5 En los procesos de fabricacin de componentes y circuitos electrnicos se emplean diferentes materiales, los cuales aportan las caractersticas elctricas y el comportamiento funcional. 
    .row.mt-5
      .col-10.offset-1
        p.mt-5.p-1(data-aos="fade-up" style="padding-left:13px !important;border-left:4px #B3009D solid;") #[strong Figura 6.] <em>Materiales</em>
        ImagenInfografica.color-acento-botones.mt-5.intro
          template(v-slot:imagen)
            figure
              img(src='@/assets/template/tema-1-11.png', alt='Texto que describa la imagen')
          .cajon.color-acento-botones(x="65.5%" y="0%" numero="b").p-3           
            p.m-0 b. En la fabricacin de componentes pasivos como las resistencias, condensadores y bobinas se utiliza una combinacin de materiales conductores y aislantes. Dentro de los materiales aislantes ms usados se encuentran el vidrio, la mica, el aire y los plsticos.
          .cajon.color-acento-botones(x="-1.2%" y="22%" numero="a").p-3             
            p.m-0 a. Para la fabricacin de conductores elctricos se suelen emplear metales como el cobre, la plata o el oro, los cuales gracias a su propiedad de conductividad y baja resistividad los hacen buenos materiales para transportar la corriente elctrica. 
          .cajon.color-acento-botones(x="60%" y="99.5%" numero="c").p-3             
            p.m-0 c. Por otro lado, el silicio o el germanio son elementos empleados en la construccin de semiconductores; el diodo, los transistores, los tiristores y los circuitos integrados son un claro ejemplo de semiconductores que han permitido la evolucin de la electrnica.
            .titulo-segundo.mt-5
    .titulo-segundo.mt-5
      #t_1_5.h2 1.5  Placa base
    figure.mt-3
      img(src="@/assets/template/tema-1-12.svg", alt="Texto que describa la imagen")
    p.mt-5 La placa base, denominada sustrato, es el material que servir de soporte para los componentes y las conexiones elctricas de un circuito. Se caracteriza por estar construida por materiales aislantes como baquelita, fibra de vidrio o resinas epxicas reforzadas con fibra de vidrio llamadas FR4.
    .titulo-segundo.mt-5
